.add-anime {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 92vh;
  text-transform: capitalize;
}

.add-anime .container {
  width: 80%;
  padding: 20px;
}

.add-anime .container h2 {
  width: 100%;
  color: #fff;
  font-size: 36px;
  text-align: center;
  margin-bottom: 10px;
}

.add-anime .container .row100 {
  position: relative;
  width: 100%;
}

.add-anime .container .row100:first-of-type,
.add-anime .container .row100:nth-of-type(3) {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}
.add-anime .container .row100:nth-of-type(2) {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.add-anime .container .row100 .col {
  position: relative;
  width: 100%;
  padding: 0 10px;
  margin: 30px 0 10px;
  transition: 0.5s;
}

.add-anime .container .row100 .inputBox {
  position: relative;
  width: 100%;
  height: 40px;
  color: #fff;
}
.add-anime .container .row100 .inputBox .err {
  position: absolute;
  left: 60px;
  top: -30px;
}

.add-anime .container .row100 .inputBox:nth-of-type(6),
.add-anime .container .row100 .inputBox:nth-of-type(3),
.add-anime .container .row100 .inputBox:first-of-type {
  grid-area: 1 / 1 / 2 / 2;
}

.add-anime .container .row100 .inputBox:nth-of-type(7),
.add-anime .container .row100 .inputBox:nth-of-type(4),
.add-anime .container .row100 .inputBox:nth-of-type(2) {
  grid-area: 1 / 2 / 2 / 3;
}

.add-anime .container .row100 .inputBox:nth-of-type(5) {
  grid-area: 1 / 3 / 2 / 4;
}

.add-anime .container .row100 .inputBox:nth-of-type(8) {
  grid-area: 3 / 1 / 4 / 5;
}

.add-anime .container .row100 .inputBox input,
.add-anime .container .row100 .inputBox.textarea textarea {
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  box-decoration-break: none;
  border: none;
  outline: none;
  font-size: 18px;
  padding: 0 10px;
  z-index: 1;
  color: #000;
}
.add-anime .container .row100 .inputBox .text {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
  font-size: 18px;
  padding: 0 10px;
  display: block;
  transition: 0.5s;
  pointer-events: none;
}
.add-anime .container .row100 .inputBox input:focus + .text,
.add-anime .container .row100 .inputBox input:valid + .text {
  top: -35px;
  left: -10px;
}

.add-anime .container .row100 .inputBox .line {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 2px;
  background: turquoise;
  transition: 0.5s;
  border-radius: 2px;
  position: none;
}
.add-anime .container .row100 .inputBox input:focus ~ .line,
.add-anime .container .row100 .inputBox input:valid ~ .line {
  height: 100%;
}

.add-anime .container .row100 .inputBox.textarea {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0;
}

.add-anime .container .row100 .inputBox.textarea textarea {
  height: 100%;
  resize: none;
}
.add-anime .container .row100 .inputBox textarea:focus ~ .text,
.add-anime .container .row100 .inputBox textarea:valid ~ .text {
  top: -35px;
  left: -10px;
}
.add-anime .container .row100 .inputBox textarea:focus ~ .line,
.add-anime .container .row100 .inputBox textarea:valid ~ .line {
  height: 100%;
}
input[type="submit"] {
  border: none;
  outline: none;
  padding: 5px 20px;
  background-color: #000;
  border: 2px solid turquoise;
  letter-spacing: 1.5px;
  color: turquoise;
  font-weight: 600;
  transition: all 0.2s ease-in-out;
}
input[type="submit"]:hover {
  background-color: turquoise;
  border: 2px solid turquoise;
  letter-spacing: 3px;
  color: #000;
}
